export default {
  data () {
    return {
      currIndex: 0,
      pageShow: false,
      page: 0,
      list: [],
      isMore: false,
      isLoading: false
    }
  },

  onLoad () {
    this.getData(1)
  },

  methods: {
    changeTab (index) {
      this.currIndex = index
      this.list = []
      this.page = 0
      this.$nextTick(() => {
        this.getData(index + 1)
      })
    },

   getData (type) {
     if (this.isMore) return
     if (this.isLoading) return

     this.isLoading = true
     this.page === 0 && this.$loading()
     this.$http.getDistributionTeamInfo({
       filter: type,
       page: this.page + 1
     }).then(res => {
      if (res.status === 200 || res.status === 400) {
        this.isLoading = false
        if (!res.data.friendres.length) {
          this.isMore = true

          return false
        }
        this.page = this.page + 1
        if (this.list.length) {
          this.list = [...this.list, ...res.data.friendres]
        } else {
          this.list = res.data.friendres  
        }
        this.pageShow = true
      } else {
        this.$toast(res.mess)
        this.isLoading = false
      }
    }).catch(() => {
      this.isLoading = false
    })
   } 
  }
}
